import React, { useState } from "react";
import myStyle from './Main.module.scss';
import BankAccount from './content/BankAccount';
import Report from './content/Report';
import Directions from './content/Directions';

function Main({imageSrc}) {
	const [activeSection, setActiveSection] = useState("Report"); //   "schedule" 
	
	const handleClick = (section) => {
    console.log(`${section} clicked`);
    // Add navigation or functionality here based on section
  };
  return (
		<div className={myStyle.main}>
			<div className={myStyle.title}>
				<div className={myStyle.buttonGroup}>
					<button className={myStyle.customButton} onClick={() => setActiveSection("Report")} > <span className={myStyle.point}></span></button>
					{/* <span className={myStyle.separator}> / </span> */}
					<button className={myStyle.customButton} onClick={() => setActiveSection("Directions")}> <span className={myStyle.point}></span></button>
					{/* <span className={myStyle.separator}> / </span> */}
					{/* <button className={myStyle.customButton} onClick={() => setActiveSection("BankAccount")}></button> */}
					<button className={myStyle.customButton} onClick={() => setActiveSection("BankAccount")}> <span className={myStyle.point}></span></button>
				</div>
			</div>
			<div className={myStyle.content}>
				{/*    */}
				{activeSection === "Report" && <Report imageSrc={imageSrc}/>}
				{activeSection === "Directions" && <Directions/>}
				{activeSection === "BankAccount" && <BankAccount />}
			</div>
    </div>
  );
}

export default Main;
